import React, { Component } from 'react';
import './ProfileComponent.css';

import profileImage from '../assets/images/profile-image.jpg';

class ProfileComponent extends Component {



    constructor() {
        super();
        this.state = {};
    }


    render() {

        let profileObject = null;

        for (let i = 0; i < this.props.passedArray.length; i++) {

            if (this.props.passedArray[i].id === this.props.profileId) {
                profileObject = this.props.passedArray[i];
            }


        }







        if (profileObject) {



            return (






                <div className="profile-container">

                    <div className="my-card">

                        <div className="my-card-image">
                            <img src={profileImage} className="my-image" />
                        </div>





                        <h3 className="my-card-title" >{profileObject.name}</h3>
                        <h4 className="my-card-sub-title">eye-color: {profileObject.eye_color}</h4>
                        <p className="my-card-content">gender : {profileObject.gender} </p>



                        <div className="my-card-panel">
                            <a className="my-card-panel-button btn-1"><i className="fas fa-envelope"></i></a>
                            <a className="my-card-panel-button btn-2"><i className="fas fa-phone-alt"></i></a>
                            <a className="my-card-panel-button btn-3"><i className="fas fa-video"></i></a>

                        </div>


                    </div>



                </div>
            )
        }

        else {
            return (
                <div className="no-selection-message">
                    <h3> please click an image  </h3></div>

            )
        }



    }


}
export default ProfileComponent;    