import React, { Component } from 'react';
import './NavbarComponent.css'

class NavbarComponent extends Component {

    constructor() {
        super();
        this.state = {}


    }


    render() {





        return (

            <div className="nav-container">
                <ul className="nav-list">

                    <li><a className="nav-links" onClick={() => { this.props.handleSelection('people') }} value="people">people 1</a></li>
                    <li><a className="nav-links" onClick={() => { this.props.handleSelection('places') }} value="places">people 2</a></li>
                    <li><a className="nav-links" onClick={() => { this.props.handleSelection('courses') }} value="courses">people 3</a></li>

                </ul>

            </div>

        )


    }

}

export default NavbarComponent;