import React, { Component } from 'react';
import listItemImage from '../../assets/images/profile-image.jpg';
import './ListItemComponent.css'

class ListItemComponent extends Component {


    constructor() {
        super();
        this.state = {
            profileItem: {}
        }
    }



    render() {

        return (


            <a className="list-item-link-container" onClick={() => { this.props.handleIdChange(this.props.item.id) }} >
                <div className="list-item-container" >



                    <div className="list-item-image-container">
                        <img src={listItemImage} alt="list item image" className="list-item-image" />
                    </div>

                    <div className="list-item-content">
                        <div>
                            <h5 className="list-item-title">{this.props.item.name}</h5>
                            <p className="list-item-subTitle" >{this.props.item.eye_color}</p>
                        </div>

                        <div>
                            <p className="list-item-footer" >{this.props.item.gender}</p>
                        </div>



                    </div>

                    <span className="more-options"> <i className="fas fa-ellipsis-h"></i> </span>



                </div>
            </a>



        )




    }


}

export default ListItemComponent;