import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import HeaderComponent from './HeaderComponent';
import NavbarComponent from './Navbar/NavbarComponent';
import MainListComponent from './MainList/MainListComponent';
import ProfileComponent from './Profile/ProfileComponent';

class App extends Component {
  constructor() {
    super();
    this.state = {
      selectedNav: "",
      all: [],
      people: [],
      places: [],
      courses: [],
      passedArray: [],
      profileId: ""

    }
    this.handleSelection = this.handleSelection.bind(this);
    this.handleChangeApp = this.handleChangeApp.bind(this);
  }

  handleChangeApp(id) {

    this.setState({
      profileId: id
    });


  }

  componentDidMount() {

    fetch('https://swapi.co/api/people').
      then(response => response.json())
      .then((data) => {

        let people1 = [], places1 = [], courses1 = [];
        for (let i = 0; i < 3; i++) {

          people1.push({ ...data.results[i], id: i });
          places1.push({ ...data.results[i + 3], id: i + 3 });
          courses1.push({ ...data.results[i + 6], id: i + 6 });
        }
        this.setState({
          people: people1,
          places: places1,
          courses: courses1,
          passedArray: people1
        })

      });



  }
  handleSelection(selected) {



    this.setState({

      passedArray: this.state[selected],
      selectedNav: selected,
      profileId: ""

    });


  }
  render() {


    return (


      <div className="app-container">
        <div>
          <div className="header-container"><HeaderComponent /></div>


          <div className="main">
            <div className="navbar-container">
              <NavbarComponent handleSelection={this.handleSelection} />
            </div>



            <div className="main-list-container"><MainListComponent selectedNav={this.state.selectedNav} handleChangeApp={this.handleChangeApp} passedArray={this.state.passedArray} /></div>


            <div className="profile-containers"><ProfileComponent passedArray={this.state.passedArray} profileId={this.state.profileId} /></div>


          </div>



        </div>

      </div>



    );
  }

}

export default App;
