import React, { Component } from 'react'
import ProfileComponent from '../Profile/ProfileComponent';
import ListItemComponent from './ListItem/ListItemComponent';
import '../index.css';
import './MainListComponent.css';
import { isLiteral } from '@babel/types';

class MainListComponent extends Component {
    constructor() {
        super();
        this.state = {
            searching: "",
            searchedItems: []

        }



    }


    componentDidMount() {


    }
    render() {





        let listItems = this.props.passedArray.map((item) => {
            return <ListItemComponent handleIdChange={

                (id) => {

                    this.props.handleChangeApp(id);

                }

            }
                key={item.id}
                item={item} />
        });




        if (this.props.selectedNav) {

            return (


                <div>
                    <div className="top-panel">
                        <h1 className="top-panel-heading">{this.props.selectedNav}</h1>
                        <div className="search-bar">
                            <span className="search-icon" ><i className="fas fa-search"></i></span>

                            <form >
                                <input className="form-input"  placeholder="search by name" name="searching" value={this.state.searching} onChange={
                                    (event) => {
                                        let { name, value } = event.target;




                                        let temp = [];
                                        for (let i = 0; i < this.props.passedArray.length; i++) {



                                            if ((this.props.passedArray[i].name).toLowerCase().includes(this.state.searching.toLowerCase())) {
                                                temp.push(this.props.passedArray[i]);

                                            }
                                        }



                                        let searchedTempItems = temp.map((item) => {
                                            return <ListItemComponent handleIdChange={

                                                (id) => {

                                                    this.props.handleChangeApp(id);

                                                }

                                            }
                                                key={item.id}
                                                item={item} />
                                        });


                                        this.setState({
                                            searchedItems: searchedTempItems
                                        })








                                        this.setState({
                                            [name]: value,
                                        });
                                    }
                                } />

                            </form>
                        </div>

                        <div className="sort-button">sort <span><i className="fas fa-angle-down"></i></span></div>

                    </div>

                    <div>{this.state.searching ? this.state.searchedItems : listItems}</div>
                </div>
            )

        }
        else {
            return (
                <div className="no-selection-message">
                    <p>please click on navbar item to show the list in a category</p>
                </div>
            )

        }
    }


}


export default MainListComponent;